main {
  flex: 1;
  width: min(100% - 2em);
  margin-inline: auto;

  article {
    display: flex;
    flex-direction: column;
    // width: min(100% - 2em);
    margin-block: 4em 2em;
    margin-inline: auto;
  }

  h1 {
    font-weight: bold;
    margin-bottom: 1em;
  }

  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1em;
  }

  p {
    margin-bottom: 1em;

    // &:last-child {
    //   margin-bottom: 0;
    // }
  }
}

label {
  display: inline-block;
  font-family: inherit;
  font-weight: bold;
  margin-bottom: 0.5em;
}

.content {
  position: relative;
  font-size: 1rem;

  &__hidden {
    @include visually-hidden;
  }

  &__item {
    &[data-open="false"] {
      display: none;
    }

    &[data-open="true"] {
      display: block;
      // position: absolute;
      // inset: 0;
      // background-color: var(--theme-bg);
    }
  }
}

.breed {
  display: flex;
  flex-direction: column;
  width: 100%;
  // padding-inline: 1em;

  &__form {
    display: flex;
    align-items: center;
    margin-block: 2em 1em;
    padding-inline: 0.25em 0.75em;
    width: 100%;
    border: 1px solid var(--theme-border);
    border-radius: 0.5em;

    label {
      margin-right: 1em;
      @include visually-hidden;
    }

    input {
      color: var(--theme-text);
      text-transform: capitalize;
      background-color: transparent;
      border: 0;
      padding: .5em;
      width: 100%;

      &::placeholder {
        color: var(--theme-border);
      }

      &:focus,
      &:focus-visible {
        outline: 1px solid transparent;
      }
    }
  }

  &__icon {
    transform: rotate(45deg);
    padding-inline: .25em;
    transition: var(--theme-transition);

    &[data-active="true"] {
      .icon__active {
          fill: var(--theme-accent);
      }
    }
  }

  &__option {
    white-space: nowrap;
    background-color: transparent;
    padding-block: .5em;
    cursor: pointer;
    transition: var(--theme-transition);

    &:hover {
      color: var(--theme-accent);
    }
  }

  &__result {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(min(20em, 100%), 1fr));
    gap: 1em;
    margin-bottom: 1em;
  }

  &__loader {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    min-height: 50dvh;
    text-align: center;
  }

  &__error {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    min-height: 50dvh;
    text-align: center;
    @include media-container(md);
    margin-top: 1em;
    margin-inline: auto;
  }

  &__item {
    display: flex;
    flex-direction: column;
    margin-block: 0.5em;
    padding: 0.75em;
    width: 100%;
    border: 1px solid var(--theme-border);
    border-radius: 0.5em;
    box-shadow: var(--theme-shadow);
    overflow: hidden;
    transition: var(--theme-transition);

    &:hover {
      transform: scale(1.01);
      box-shadow: var(--theme-shadow-alt);
    }

    img {
      width: 100%;
      min-height: 15em;
      object-fit: cover;
    }

    h2 {
      font-size: 1.25rem;
      margin-bottom: 1em;
    }

    h3 {
      font-weight: bold;
      margin-bottom: 0.25em;
    }

    p {
      margin-bottom: 1em;
    }
  }

  &__image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    max-height: 15em;
    aspect-ratio: 4/3;
    width: 100%;
    margin-inline: auto;
    margin-bottom: 1em;
    background-color: var(--theme-bg);
    border: 1px solid var(--theme-border);
  }

  &__weight {
    p {
      margin-bottom: 0.15em;
    }

    :last-child {
      margin-bottom: 1em;
    }
  }

  &__links {
    margin-top: auto;

    a {
      display: inline-block;
      text-decoration: none;
      border: 1px solid var(--theme-text);
      border-radius: .5em;
      margin: .25em .25em .25em 0;
      padding: .25em .5em;
      cursor: pointer;
      @include transition;

      &:hover {
        color: var(--theme-text-alt);
        background-color: var(--theme-text);
      }
    }
  }

  &__front,
  &__back {
    display: block;
    margin-inline: 0.75em;
    z-index: -1;
    transition: var(--theme-transition);
  }

  &__front {
    &[data-open="false"] {
      opacity: 0;
      max-height: 0;
      overflow: hidden;
    }

    &[data-open="true"] {
      opacity: 1;
      max-height: 99rem;
      margin-bottom: 1em;
      transition: var(--theme-transition);
    }
  }

  &__back {
    &[data-open="false"] {
      opacity: 0;
      max-height: 0;
      overflow: hidden;
    }

    &[data-open="true"] {
      opacity: 1;
      max-height: 99rem;
      margin-bottom: 1em;
      transition: var(--theme-transition);
    }
  }

  &__expand {
    color: var(--theme-text-alt);
    background-color: var(--theme-accent);
    border: 1px solid var(--theme-accent);
    border-radius: 0.25em;
    margin-top: auto;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: var(--theme-transition);

    &:hover {
      color: var(--theme-accent);
      background-color: var(--theme-bg);
    }
  }
}

// Breakpoint
@include media-breakpoint-up(lg) {
  main {
    @include media-container(xl);

    section {
      article {
        @include media-container(xl);
      }
    }
  }

  .breed {
    form {
      margin-inline: auto;
      @include media-container(sm);
    }
  }
}
