$color-transparent: transparent;

// Theme
:root {
  --theme-font: "Inter", sans-serif;
  --theme-bg: #ffffff;
  --theme-bg-alt: #f6f8fa;
  --theme-bg-secondary: #20232a;
  --theme-text: #333333;
  --theme-text-alt: #ffffff;
  --theme-accent: #0B4DB7;
  --theme-border: #dfe1e5;
  --theme-shadow: 0 2px 15px rgba(0, 0, 0, 0.08);
  --theme-shadow-alt:  0 4px 20px rgba(0, 0, 0, 0.11);
  --theme-transition: 0.25s opacity ease-in-out, 0.25s transform ease-in-out,
    0.25s border-color ease-in-out, .35s max-height ease-in-out, 0.25s background-color ease-in-out;
}